.home {
  min-height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h3 {
  text-align: center;
  margin-bottom: 24px;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.upload {
  width: 720px;
  height: 360px;
  border-radius: 6px;
  border: 1px dashed #d9d9d9;
  text-align: center;
  cursor: pointer;
  overflow-y: scroll;
  overflow-x: auto;
}

.upload p.toast {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.upload p.toast {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.upload p span {
  font-size: 14px;
  font-weight: 600;
  color: #606266;
}

.input {
  display: none;
  overflow: visible;
  touch-action: manipulation
}

.error {
  width: 720px;
  text-align: left;
  height: 40px;
  line-height: 40px;
  font-size: 13px;
  font-weight: 500;
}

.error span {
  color: red;
  transition: ease-in-out .5s;
}

label span {
  font-weight: 600;
  font-size: 14px;
}

label input {
  width: 270px;
  height: 30px;
  padding: 4px 11px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  color: #000000d9;
  font-size: 14px;
}

label input:focus {
  border-color: #40a9ff;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, .2);
  border-right-width: 1px !important;
  outline: 0;
}

input::-webkit-input-placeholder {
  font-size: 14px;
  font-weight: 400;
  color: #cacaca;
}

.submit {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  height: 32px;
  white-space: nowrap;
  cursor: pointer;
  background-color: #409eff;
  border: 2px solid #409eff;
  color: #fff;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 24px;
  transition: .1s;
  font-weight: 500;
  user-select: none;
  padding: 8px 15px;
  font-size: 14px;
  border-radius: 4px;
}

.submit:hover {
  background-color: rgb(102, 177, 255);
  border-color: transparent;
}